@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
.warehouse-container
{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: stretch;
}

.table
{
    display: flex;
    justify-content: space-between;
    padding: 10px 0;
    width: 50vh;
}
.table p{
    flex-basis: 50%;
}
.warehouse
{
    box-shadow: 2px 2px 10px -6px black;
    color: black;
    padding: 20px;
    
    border-radius: 20px;
    font-family: 'Courier New', Courier, monospace;
    width: 75vw;
    cursor: pointer;
    text-decoration: none;
}
.warehouse h2
{
    margin: 0;
    padding-bottom: 10px;
}
.warehouse-link
{
    margin: 20px 5px;
    text-decoration: none;
}
.warehouse-expand-card
{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.warehouse-view-header
{
    margin:20px 0;
    font-weight: 900;
}

.customFieldContainer
{
    margin: 20px 0;
    border: 1px solid black;
    padding: 10px;
}

@media screen and (min-width: 768px)
{
    .warehouse
    {
        width: 25vw;
    }
    .table
    {
        width: 50vh;
        word-break: break-all;
    }
    .edit-field,select
    {
        width: 500px!important;
    }
    select
    {
        padding:10px 0px;   
        border: none;
        border-bottom: 1px solid black;
        margin-bottom: 10px;
        outline: none;
    }
}
input[type="text"],input[type="number"]
{
    display: block;
    width: 100%;
    margin: 0px 0 10px 0;
    padding: 2px;
    outline: none;
    border: none;
    border-bottom: 1px solid black;
    font-size: 1.2em;
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Poppins;
    letter-spacing: 0.7px;
}
body{
    padding: 0 20px;
}
button{
    background-color: black;
    color: white;
    padding:10px 20px;
    border: none;
    outline: none;
    cursor: pointer;
    margin-right: 20px;
    min-width: 100px;
    margin-bottom: 20px;
}

@media screen and (min-width:768px)
{
    body{
        padding: 0 100px;
    }   
}
